<template>
    <div class="content-wrap">
        <div class="topContent">
            <div class="title">基本信息：</div>
            <div class="items">
                <div class="item">
                    <span>云机编号：</span>
                    <span>{{baseinfo.pad_code ? baseinfo.pad_code : '-'}}</span>
                </div>
                <div class="item">
                    <span>云机名称：</span>
                    <span>{{baseinfo.pad_name ? baseinfo.pad_name : '-'}}</span>
                </div>
                <div class="item">
                    <span>云机状态：</span>
                    <span>{{baseinfo.pad_status == 0 ? baseinfo.pad_status : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>所属用户：</span>
                    <span>{{baseinfo.user_name ? baseinfo.user_name : '-'}}</span>
                </div>
                <div class="item">
                    <span>所属分组：</span>
                    <span>{{baseinfo.pad_group_name ? baseinfo.pad_group_name : '-'}}</span>
                </div>
                <div class="item">
                    <span>绑定时间：</span>
                    <span>{{baseinfo.bind_time ? baseinfo.bind_time : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>到期时间：</span>
                    <span>{{baseinfo.expire_time ? baseinfo.expire_time : '-'}}</span>
                </div>
                <div class="item">
                    <span>安装应用数：</span>
                    <span>{{baseinfo.app_count ? baseinfo.app_count : '-'}}</span>
                </div>
                <div class="item">
                    <span>文件分发数：</span>
                    <span>{{baseinfo.file_count ? baseinfo.file_count : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>连接中：</span>
                    <span>{{baseinfo.pad_status == 2 ? '是' : '否'}}</span>
                </div>
            </div>
        </div>
        <div class="bottomContent">
            <div class="title">参数信息：</div>
            <div class="items">
                <div class="item">
                    <span>序列号：</span>
                    <span>{{detailInfo.sn ? detailInfo.sn  : '-'}}</span>
                </div>
                <div class="item">
                    <span>IP地址：</span>
                    <span>{{detailInfo.ip ? detailInfo.ip  : '-'}}</span>
                </div>
                <div class="item">
                    <span>物理机IP地址：</span>
                    <span>{{detailInfo.host_ip ? detailInfo.host_ip  : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>安卓版本：</span>
                    <span>{{detailInfo.os_ver ? detailInfo.os_ver  : '-'}}</span>
                </div>
                <div class="item">
                    <span>型号：</span>
                    <span>{{detailInfo.model ? detailInfo.model  : '-'}}</span>
                </div>
                <div class="item">
                    <span>Android ID：</span>
                    <span>{{detailInfo.android_id ? detailInfo.android_id  : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>IMEI：</span>
                    <span>{{detailInfo.imei ? detailInfo.imei  : '-'}}</span>
                </div>
                <div class="item">
                    <span>IMSI：</span>
                    <span>{{detailInfo.imsi ? detailInfo.imsi  : '-'}}</span>
                </div>
                <div class="item">
                    <span>已开机时间：</span>
                    <span>{{detailInfo.boot_time ? detailInfo.boot_time  : '-'}}</span>
                </div>
            </div>
            <div class="items">
                <div class="item">
                    <span>内存使用量：</span>
                    <span>{{detailInfo.mem_info ? detailInfo.mem_info  : '-'}}</span>
                </div>
                <div class="item">
                    <span>存储使用量：</span>
                    <span>{{detailInfo.storage_info ? detailInfo.storage_info  : '-'}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { baseInfo, detailInfo } from '../../api/phone';
import { parseTime } from '../../utils/index';
import { mapState } from 'vuex'
export default {
    data() {
        return {
            userId: '',
            padId: '',
            baseinfo: {},
            detailInfo: {}
        }
    },
    computed: {
        ...mapState({
            phones: state => state.phone.phones
        }),
    },
    mounted() {
        console.log(this.phones)
        this.userId = this.phones.user_id;
        this.padId = this.phones.pad_id;
        this.getBaseInfo();
        this.getDetailInfo();
    },
    methods: {
        getBaseInfo() {
            let params = new Object();
            params.padId = this.padId;
            params.userId  = this.userId;
            baseInfo(params)
            .then(res => {
                if (res.code == 200) {
                  this.baseinfo = res.data;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        getDetailInfo() {
            let params = new Object();
            params.padId = this.padId;
            params.userId  = this.userId;
            detailInfo(params)
            .then(res => {
                if (res.code == 200) {
                    this.detailInfo = res.data ? res.data : {}
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .bottomContent{
        margin-top: 30px;
    }
    .topContent,.bottomContent{
        padding: 20px 15px;
        border: 1px solid #eee;
        .title{
            border-left: 3px solid #409EFF;
            padding-left: 6px;
            margin-bottom: 20px;
        }
        .items{
            padding: 0 0 25px 6px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .item{
                width: calc(100%/3)
            }
        }
    }
}
</style>


